import React from 'react';
import Components from 'components';
import { connect } from 'react-redux';
import Promise from 'bluebird';
import Actions from 'flux/actions';

class Offers extends Components {

  render() {
    return (
      // <iframe src={this.props.billing.endpoint.extended} className="billing-system-offers-bo" />
      <iframe src="http://intuiteev.knowledgeowl.com/help" className="billing-system-offers-bo" />
    );
  }
}

function stateToProps(state) {
  return {
    tenant: state.get('tenant'),
    billing: state.get('backoffice').get('billing').toJS(),
  };
}

const _components = { default: connect(stateToProps)(Offers) };

export default class TenantProxy {
  static get(tenant) {
    return _components[tenant] ? _components[tenant] : _components.default;
  }
}



